@import "../../assets/scss/app";

.shopItems {
  padding-top: 48px;

  @include desktop() {
    padding-top: 53px;
  }

  .menu {
    margin-top: 23px;

    @include desktop() {
      margin-top: 13px;
    }

    .inner {
      margin-left: -10px;
      margin-right: -10px;
      display: flex;
      flex-wrap: wrap;

      @include desktop() {
        margin-left: -20px;
        margin-right: -20px;
      }

      .item {
        cursor: pointer;
        padding: 10px 20px;
        border-radius: 37px;
        font-weight: bold;
        font-size: 30px;
        line-height: 37px;
        margin: 20px 10px 0;

        @include desktop() {
          padding: 20px 90px;
          margin: 30px 20px 0;
        }

        p {
          font-weight: bold;
          font-size: 24px;
          line-height: 120%;

          @include desktop() {
            font-size: 30px;
          }
        }

        &_light {
          box-shadow: inset -3px -3px 5px #FFFFFF, inset 3px 3px 5px #D6D6E1;

          p {
            color: #292D32;
            opacity: 0.2;
          }
        }

        &_dark {
          box-shadow: inset -3px -3px 5px #30343A, inset 3px 3px 5px #24262C;

          p {
            color: #FFF;
            opacity: 0.2;
          }
        }

        &_selected_light {
          box-shadow: -3px -3px 5px #FFFFFF, 0px 0px 5px #D6D6E1;

          p {
            opacity: 1;
          }
        }

        &_selected_dark {
          box-shadow: -3px -3px 5px #30343A, 3px 3px 5px #24262C;

          p {
            opacity: 1;
          }
        }


      }
    }
  }

  .cards {
    display: flex;
    flex-direction: column;

    @include desktop() {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-column-gap: 30px;
    }
  }

}