@import "../../assets/scss/app";

.ShopFirstBlock {
  padding-top: 115px;
  display: flex;
  flex-direction: column;

  @include desktop() {
    padding-top: 80px;
    flex-direction: row;
    align-items: center;
  }

  .firstBlock {
    .text {
      @include onlyDesktop() {
        margin-top: 80px;
        width: 395px;
        font-weight: 300;
        font-size: 31px;
        line-height: 38px;
      }
    }
  }

  .secondBlock {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 35px;

    @include desktop() {
      flex-grow: 1;
      @include center();
      margin-top: 0;
    }

    img {
      width: 300px;

      @include desktop() {
        height: 500px;
        width: auto;
      }
    }

    .text {
      @include onlyMobile(){
        margin-top: 40px;
        text-align: center;
        font-weight: 300;
        font-size: 31px;
        line-height: 38px;
      }
    }
  }

}